.pagination-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.items-per-page {
  margin-right: 10px;
  margin-left: 10px;
  max-width: 50px;
}

.page-navigation {
  display: flex;
  align-items: center;
}

.page-button {
  margin-right: 10px;
  padding: 8px 16px;
  background-color: #4caf50;
  color: #fff;
  border: none;
  cursor: pointer;
  border-radius: 10px;
}

.page-button:hover {
  background-color: #45a049;
}

.page-button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

.page-input {
  width: 50px;
  margin-right: 5px;
  text-align: center;
}

.total-pages {
  margin-right: 10px;
}
