.app-container {
  padding: 20px;
}

.search-input {
  margin-bottom: 10px;
}

.cars-table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 10px;
}

.cars-table th,
.cars-table td {
  border: 1px solid #ccc;
  padding: 8px;
}

.pagination {
  margin-top: 20px;
}

.add-car-button {
  margin-top: 20px;
  border-radius: 10px;
  cursor: pointer;
}

.add-car-button:hover {
  cursor: pointer;
  color: #fff;
  background-color: #4caf50;
}

.error-message {
  color: #f00;
}
