.modal-container {
  padding: 20px;
}

.input-field {
  width: 100%;
  padding: 8px;
  border: 1px solid #ccc;
}

.save-button {
  margin-top: 10px;
  margin-right: 10px;
  background-color: #4caf50;
  color: #fff;
  padding: 8px 16px;
  border: none;
  cursor: pointer;
  border-radius: 10px;
}

.cancel-button {
  margin-top: 10px;
  border-radius: 10px;
}
