.Loader {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
}

.Loader__content {
  width: 2em;
  height: 2em;
  margin: 1em auto;
  border-radius: 50%;
  border: 0.3em solid #ddd;
  border-top-color: #000;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}